//
// input date
//

input[type=date], 
input[type=datetime-local] {
	display: inline-block;
	position: relative;
}

input[type=date]::-webkit-calendar-picker-indicator, 
input[type=datetime-local]::-webkit-calendar-picker-indicator {
  background: transparent;
	bottom: 0;
	color: transparent;
	cursor: pointer;
	height: auto;
	left: 0;
	position: absolute;
	right: 0;
	top: 0;
	width: auto;
}